import React, { Component } from "react";
import "./releasecontent.scss";
var u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1;
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
export default class release extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showElem: "block",
      day: 1,
      iosshow: false, //ios
    };
  }
  componentWillMount() {
    //DOM

    /*
           1  day0
    
          */
    if (isiOS) {
      this.setState({
        iosshow: true,
      });
    } else {
      this.setState({
        iosshow: false,
      });
    }
    // console.log(Number(this.getUrlParam("day"))>1);
    this.setState({
      day: this.getUrlParam("day"),
    });
  }
  componentDidMount() {
    //DOM
    console.log(this.props);
  }
  getUrlParam = (name) => {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = this.props.location.search.substr(1).match(reg);
    console.log(window.location);
    if (r != null) {
      return decodeURI(r[2]);
    }
    return ""; //return;undefined
  };
  Confirm = () => {
    //IOSNativenAlertConfirm 
    //IOSNativenAlertCancel 
    if (isiOS) {
      try {
        window.webkit.messageHandlers.IOSNativenAlertConfirm.postMessage("");
      } catch (e) {
        console.log(e);
        console.log(this);

        this.setState({
          showElem: "none",
        });
      }
    } else {
      try {
        window.app.androidClickAction();
      } catch (e) {
        //TODO handle the exception
        console.log(e);
        this.setState({
          showElem: "none",
        });
      }
    }
  };
  render() {
    return (
      <div className="releasecontent iosView">
        <div style={{ display: this.state.showElem }}>
          <div className="releaseHome">
            <div className="releaseccontent">
              {this.state.day >= 1 ? (
                <div>
                  <header>
                    <span>{this.state.day}</span>
                  </header>
                  <img
                    src={require("../assets/zhuangshi.png")}
                    alt=""
                    className="img_shili"
                  />
                </div>
              ) : (
                <p className="zhanwei"></p>
              )}
              <ul></ul>

              <div className="mycontent">
                
                <br />
                1.5 <br />
                2. <br />
                3.
              </div>
              <div className="know" onClick={() => this.Confirm()}>
                
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
