import React, { Component } from "react";
import "./band_paid.scss";
import Umargin from "../../Goolbal/Umargin";
import Uname from "../../Goolbal/Uname";
import Not_available from "../../Goolbal/Not_available";
import axios from "../../axios/index";
import Customer from "../../Goolbal/Customer";
class band_paid extends React.Component {
  state = {
    resultObject: {},
    limitFunctionStr: ""
  };
  limit = item => {
    console.log(item);
    //  proxy/publish/deal/activity/login/,all/
    switch (item) {
      case "proxy":
        return "";
        break;
      case "publish":
        return "";
        break;
      case "deal":
        return "";
        break;
      case "activity":
        return "";
        break;
      case "login":
        return "";
        break;
      case "all":
        return "";
        break;
      default:
        return "- -";
    }
  };
  componentDidMount() {
    /* 
		 
		 */
    const _this = this; //thisthis
    // axios.get('https://5b5e71c98e9f160014b88cc9.mockapi.io/api/v1/lists')
    axios
      .post("user/json/getPersonalBond", {
        userId:_this.props.match.params.userId,
        pageSize: 100,
        pageIndex: 1
      })
      .then(function(response) {
        console.log(response.data.dataList);
        if (response.data.code == 10000) {
          _this.setState({
            resultObject: response.data.resultObject,
            isLoaded: true
          });
          var str = "";
          response.data.resultObject.limitFunction.map(item => {
            str += _this.limit(item);
            _this.setState({
              limitFunctionStr: str
            });
          });
        } else {
          _this.setState({
            isLoaded: false
          });
        }
      })
      .catch(function(error) {
        console.log(error);
        _this.setState({
          isLoaded: false,
          error: error
        });
      });
  };
  render() {
    const {history,match} = this.props;
    return (
      <div className="band_paid">
        <div className="band_paid_zhanwei"></div>
        <div className="band_paid_home">
          <Uname
            utitles=""
            ucontent={this.state.resultObject.punishReason}
            recording="true"
            history = {history}
            match={match}
          />
          <Uname
            utitles=""
            ucontent={`${this.state.limitFunctionStr}`}
          />
        </div>
        <Customer/>
      </div>
    );
  }
}
export default band_paid;
