import React, { Component } from "react";
import "./releasecontent.scss";
var u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1;
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios

export default class withdraw extends React.Component {
  determine = () => {
    if (isiOS) {
      try {
        window.webkit.messageHandlers.IOSNativenAlertConfirm.postMessage("");
      } catch (e) {
       
        console.log(e);
      }
    } else {
      try {
        window.app.androidCloseWithdrawalRule();
      } catch (e) {
        //TODO handle the exception
        console.log(e);
  
      }
    }
  };
  state = {
    rule: [
      {
        name: "",
        value: "0.70%",
      },
      {
        name: "",
        value: " 2",
      },
      {
        name: "",
        value: " 50",
      },
      {
        name: "",
        value: " 50,000",
      },
    ],
  };
  render() {
    return (
      <div className="withdraw">
        <h1></h1>

        <ul className="withdrawHome">
          {this.state.rule.map((item, key) => (
            <li ke={key + 10}>
              <p>{item.name}</p>
              <p>{item.value}</p>
            </li>
          ))}
          <li key="14">
            ,,
          </li>
        </ul>

        <button onClick={() => this.determine()}></button>
      </div>
    );
  }
}
